import React from 'react';
import { motion } from 'framer-motion';

import garant1 from '../../src/img/garant1.png';
import garant2 from '../../src/img/garant2.png';
import garant3 from '../../src/img/garant3.png';
import garant4 from '../../src/img/garant4.png';

const textAnimation = {
  hidden: {
    y: -60,
    opacity: 0,
  },
  visible: (custom) => ({
    y: 0,
    opacity: 1,
    transition: { delay: custom * 0.2 },
  }),
};

const Services = () => {
  return (
    <motion.section
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true }}
      className="services"
      id="services">
      <div className="container">
        <h1 className="services__title">      </h1>
        <div className="services__items">
          <motion.div variants={textAnimation} className="services__item">
            <h3 className="services__item-title"></h3>
            <p className="services__item-suptitle">
                 <strong> </strong>
            </p>
            <p className="services__item-text">
               ,   -.  8    40+ 
                  .
            </p>
            <p className="services__item-text">    .</p>
          </motion.div>
          <motion.div variants={textAnimation} className="services__item">
            <h3 className="services__item-title"></h3>
            <p className="services__item-suptitle">
                 <strong> </strong>
            </p>
            <p className="services__item-text">
                ,       
            </p>
            <p className="services__item-text">
                       .
            </p>
          </motion.div>
          <motion.div variants={textAnimation} className="services__item">
            <h3 className="services__item-title"></h3>
            <p className="services__item-suptitle">
                 <strong> </strong>
            </p>
            <p className="services__item-text">
                    CMS?   , 
              ?        
               .
            </p>
            <p className="services__item-text">    .</p>
          </motion.div>
        </div>
        <div className="services__guarantee">
          <motion.div custom={0.5} variants={textAnimation} className="services__guarantee-item">
            <div className="services__guarantee-img">
              <img src={garant1} alt="garant-1" />
            </div>
            <div className="services__guarantee-title"> </div>
            <div className="services__guarantee-desc">
                    .
            </div>
          </motion.div>
          <motion.div custom={1} variants={textAnimation} className="services__guarantee-item">
            <div className="services__guarantee-img">
              <img src={garant2} alt="garant-1" />
            </div>
            <div className="services__guarantee-title"> </div>
            <div className="services__guarantee-desc">
                  ,     .
            </div>
          </motion.div>
          <motion.div custom={1.5} variants={textAnimation} className="services__guarantee-item">
            <div className="services__guarantee-img">
              <img src={garant3} alt="garant-1" />
            </div>
            <div className="services__guarantee-title"> </div>
            <div className="services__guarantee-desc">
                        .
            </div>
          </motion.div>
          <motion.div custom={2} variants={textAnimation} className="services__guarantee-item">
            <div className="services__guarantee-img">
              <img src={garant4} alt="garant-1" />
            </div>
            <div className="services__guarantee-title"> </div>
            <div className="services__guarantee-desc">
                          .
            </div>
          </motion.div>
        </div>
      </div>
    </motion.section>
  );
};

export default Services;
