import React from 'react';
import { motion } from 'framer-motion';

import programmist from '../../src/img/man.jpg';
import icon1 from '../../src/img/about-1.svg';
import icon2 from '../../src/img/about-2.svg';
import icon3 from '../../src/img/about-3.svg';
import icon4 from '../../src/img/about-4.svg';
import icon5 from '../../src/img/about-5.svg';
import icon6 from '../../src/img/about-6.svg';
import icon7 from '../../src/img/about-7.svg';
import icon8 from '../../src/img/about-8.svg';

const textAnimation = {
  hidden: {
    y: -60,
    opacity: 0,
  },
  visible: (custom) => ({
    y: 0,
    opacity: 1,
    transition: { delay: custom * 0.2 },
  }),
};

const About = () => {
  return (
    <section className="about" id="about">
      <div className="container">
        <h3 className="about__title-hidden"> ?</h3>
        <div className="about__inner">
          <motion.div
            className="about__left"
            initial="hidden"
            whileInView="visible"
            custom={2}
            variants={textAnimation}
            viewport={{ once: true }}>
            <h3 className="about__title"> ?</h3>
            <img src={programmist} alt="programmist" />
          </motion.div>
          <div className="about__items">
            <motion.div
              className="about__item"
              initial="hidden"
              whileInView="visible"
              custom={2}
              variants={textAnimation}
              viewport={{ once: true }}>
              <div className="about__img-box">
                <img src={icon1} alt="icon" />
              </div>
              <div className="about__info">
                <div className="about__info-title"> </div>
                     ,      
                 .
              </div>
            </motion.div>
            <motion.div
              className="about__item"
              initial="hidden"
              whileInView="visible"
              custom={2}
              variants={textAnimation}
              viewport={{ once: true }}>
              <div className="about__img-box">
                <img src={icon2} alt="icon" />
              </div>
              <div className="about__info">
                <div className="about__info-title">   </div>
                    -      .
              </div>
            </motion.div>
            <motion.div
              className="about__item"
              initial="hidden"
              whileInView="visible"
              custom={2}
              variants={textAnimation}
              viewport={{ once: true }}>
              <div className="about__img-box">
                <img src={icon3} alt="icon" />
              </div>
              <div className="about__info">
                <div className="about__info-title">  </div>
                      .    
                   8-10 .
              </div>
            </motion.div>
            <motion.div
              className="about__item"
              initial="hidden"
              whileInView="visible"
              custom={2}
              variants={textAnimation}
              viewport={{ once: true }}>
              <div className="about__img-box">
                <img src={icon4} alt="icon" />
              </div>
              <div className="about__info">
                <div className="about__info-title">  </div>
                     ,    ,
                    .
              </div>
            </motion.div>
            <motion.div
              className="about__item"
              initial="hidden"
              whileInView="visible"
              custom={2}
              variants={textAnimation}
              viewport={{ once: true }}>
              <div className="about__img-box">
                <img src={icon5} alt="icon" />
              </div>
              <div className="about__info">
                <div className="about__info-title"> </div>
                     30 000,     .
              </div>
            </motion.div>
            <motion.div
              className="about__item"
              initial="hidden"
              whileInView="visible"
              custom={2}
              variants={textAnimation}
              viewport={{ once: true }}>
              <div className="about__img-box">
                <img src={icon6} alt="icon" />
              </div>
              <div className="about__info">
                <div className="about__info-title">  </div>
                      .   
                 1 .
              </div>
            </motion.div>
            <motion.div
              className="about__item"
              initial="hidden"
              whileInView="visible"
              custom={2}
              variants={textAnimation}
              viewport={{ once: true }}>
              <div className="about__img-box">
                <img src={icon7} alt="icon" />
              </div>
              <div className="about__info">
                <div className="about__info-title"> </div>
                             
                .
              </div>
            </motion.div>
            <motion.div
              className="about__item"
              initial="hidden"
              whileInView="visible"
              custom={2}
              variants={textAnimation}
              viewport={{ once: true }}>
              <div className="about__img-box">
                <img src={icon8} alt="icon" />
              </div>
              <div className="about__info">
                <div className="about__info-title"> </div>
                   ,    .
              </div>
            </motion.div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
